body {
  font-size: 18px;
  padding: 2rem;
  color: #eee;
  background: #333;
}

.App {
  text-align: center;
  width: 800px;
  margin: auto;
}

.question {
  display: flex;
  height: 2rem;
}

input {
  flex: 1 0;
  margin-right: 1rem;
  font-size: 1rem;
  padding: 0.25rem 0.5rem;
}

button {
  flex: 0 0 20rem;
  font-size: 1rem;
  cursor: pointer;
}

.answer img {
  width: 100%;
  border-radius: 2rem;
}

a.hovered {
  color: red;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

